//frontend/pages/payment/bkash.js
import { useState } from "react";

export default function ManualBkashPayment() {
  const [bKashNumber, setBKashNumber] = useState("");
  const [amount, setAmount] = useState("");
  const [trxId, setTrxId] = useState("");
  const [status, setStatus] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // Backend এ POST request পাঠাবে trxID verification এর জন্য
      const res = await fetch("/api/manual-bkash", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ bKashNumber, amount, trxId })
      });

      const data = await res.json();
      if (data.success) {
        setStatus(`✅ Payment Successful! Transaction ID: ${trxId}`);
      } else {
        setStatus("❌ Payment Failed! Invalid Transaction ID.");
      }

    } catch (err) {
      console.error(err);
      setStatus("❌ Payment Error! Check console.");
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100 p-6">
      <h1 className="text-3xl font-bold mb-6">bKash Payment Verification</h1>

      <form onSubmit={handleSubmit} className="bg-white p-6 rounded-xl shadow-md w-full max-w-md space-y-4">
        <div>
          <label className="block text-gray-700 mb-1">bKash Number</label>
          <input
            type="text"
            value={bKashNumber}
            onChange={(e) => setBKashNumber(e.target.value)}
            placeholder="Enter your bKash number"
            className="w-full border p-2 rounded"
            required
          />
        </div>

        <div>
          <label className="block text-gray-700 mb-1">Amount (BDT)</label>
          <input
            type="number"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            placeholder="Enter amount"
            className="w-full border p-2 rounded"
            required
          />
        </div>

        <div>
          <label className="block text-gray-700 mb-1">Transaction ID</label>
          <input
            type="text"
            value={trxId}
            onChange={(e) => setTrxId(e.target.value)}
            placeholder="Enter transaction ID"
            className="w-full border p-2 rounded"
            required
          />
        </div>

        <button type="submit" className="w-full bg-blue-600 text-white p-3 rounded-xl font-bold">
          Verify Payment
        </button>
      </form>

      {status && <p className="mt-4 text-center text-lg">{status}</p>}
    </div>
  );
}
